import { CurrencyAmount, SupportedChainId, Token } from '@uniswap/sdk-core'
import { nativeOnChain } from '@uniswap/smart-order-router'
import { useWeb3React } from '@web3-react/core'
import { TEST_MUTANT_COLLECTION } from 'constants/nftProjects'
import { useERC721Contract, useMutantContract } from 'hooks/useContract'
import JSBI from 'jsbi'
import { useSingleCallResult } from 'lib/hooks/multicall'
import { useMemo } from 'react'

const MUTANT_CONTRACT_INFO: {
  [chainId: number]: {
    collection: Token
  }
} = {
  [SupportedChainId.POLYGON_MUMBAI]: {
    collection: TEST_MUTANT_COLLECTION.collection,
  },
  [SupportedChainId.MAINNET]: {
    collection: TEST_MUTANT_COLLECTION.collection,
  },
}

export interface MutantInfo {
  contractAddress: string | undefined
  balanceOf: number | undefined
  totalSupply: number | undefined
  paused: boolean | undefined
  maxTotalSupply: number | undefined
  withdrawableDividend: CurrencyAmount<Token> | undefined
  totalClaimed: CurrencyAmount<Token> | undefined
  totalDistributed: CurrencyAmount<Token> | undefined
  totalWithdrawn: CurrencyAmount<Token> | undefined
}

export const MUTATOR_CONTRACT_ADDRESS = '0xD307e780C92249c7DAa3465EEfd96D5A2Cbd8e1B'

export function useMutantInfo(): MutantInfo {
  const { chainId, account } = useWeb3React()
  const native = chainId ? nativeOnChain(chainId) : undefined

  const contractAddress = useMemo(
    () => (chainId ? MUTANT_CONTRACT_INFO[chainId].collection.address : undefined),
    [chainId]
  )

  const accountArg = useMemo(() => [account ?? undefined], [account])
  const mutantContract = useMutantContract(contractAddress)
  const balanceOf = useSingleCallResult(mutantContract, 'balanceOf', accountArg)
  const totalSupply = useSingleCallResult(mutantContract, 'totalSupply')
  const paused = useSingleCallResult(mutantContract, 'paused')
  const maxTotalSupply = useSingleCallResult(mutantContract, 'maxSupply')

  const withdrawableDividend = useSingleCallResult(mutantContract, 'withdrawableDividendOf', [account ?? undefined])
  const totalClaimed = useSingleCallResult(mutantContract, 'withdrawnDividendOf', [account ?? undefined])
  const totalDistributed = useSingleCallResult(mutantContract, 'totalDistributed')
  const totalWithdrawn = useSingleCallResult(mutantContract, 'totalWithdrawn')

  return useMemo(() => {
    const memo: MutantInfo = {
      contractAddress,
      balanceOf: undefined,
      totalSupply: undefined,
      paused: undefined,
      maxTotalSupply: undefined,
      withdrawableDividend: undefined,
      totalClaimed: undefined,
      totalDistributed: undefined,
      totalWithdrawn: undefined,
    }

    if (!chainId) return memo
    else {
      if (
        !balanceOf?.loading &&
        !totalSupply?.loading &&
        !paused?.loading &&
        !maxTotalSupply?.loading &&
        !withdrawableDividend?.loading &&
        !totalClaimed?.loading &&
        !totalDistributed?.loading &&
        !totalWithdrawn?.loading
      ) {
        if (
          balanceOf?.error ||
          totalSupply?.error ||
          paused?.error ||
          maxTotalSupply?.error ||
          withdrawableDividend?.error ||
          totalClaimed?.error ||
          totalDistributed?.error ||
          totalWithdrawn?.error
        ) {
          console.error('Failed to load Mutant info')
          return memo
        }
        memo.balanceOf = parseInt(balanceOf?.result?.[0].toString())
        memo.totalSupply = parseInt(totalSupply?.result?.[0].toString())
        memo.paused = paused?.result?.[0]
        memo.maxTotalSupply = parseInt(maxTotalSupply?.result?.[0].toString())
        memo.withdrawableDividend = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(withdrawableDividend?.result?.[0] ?? 0))
          : undefined
        memo.totalClaimed = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(totalClaimed?.result?.[0] ?? 0))
          : undefined
        memo.totalDistributed = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(totalDistributed?.result?.[0] ?? 0))
          : undefined
        memo.totalWithdrawn = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(totalWithdrawn?.result?.[0] ?? 0))
          : undefined

        return memo
      }
      return memo
    }
  }, [
    contractAddress,
    native,
    chainId,
    balanceOf,
    totalSupply,
    paused,
    maxTotalSupply,
    withdrawableDividend,
    totalClaimed,
    totalDistributed,
    totalWithdrawn,
  ])
}

export function useNftApprovalStatus(nftContractAddress: string | undefined, contract: string): boolean {
  const { account } = useWeb3React()
  const nftContract = useERC721Contract(nftContractAddress ?? undefined)
  const approvalRes = useSingleCallResult(nftContract ?? undefined, 'isApprovedForAll', [
    account ?? undefined,
    contract ?? undefined,
  ])
  const approvalState: boolean = useMemo(() => {
    if (approvalRes?.result?.[0] === true) return true
    if (approvalRes?.result?.[0] === false) return false
    return false
  }, [approvalRes])
  return approvalState
}
