import { CurrencyAmount, Token } from '@uniswap/sdk-core'
import { SupportedChainId } from '@uniswap/sdk-core'
import { useWeb3React } from '@web3-react/core'
import { TEST_COLLECTION } from 'constants/nftProjects'
import { useERC721Contract } from 'hooks/useContract'
import JSBI from 'jsbi'
import { useSingleCallResult } from 'lib/hooks/multicall'
import { useMemo } from 'react'

import { nativeOnChain } from '../../constants/tokens'

const NFT_CONTRACT_INFO: {
  [chainId: number]: {
    collection: Token
  }
} = {
  [SupportedChainId.POLYGON_MUMBAI]: {
    collection: TEST_COLLECTION.collection,
  },
  [SupportedChainId.MAINNET]: {
    collection: TEST_COLLECTION.collection,
  },
}

interface ERC721Info {
  contractAddress: string | undefined
  balanceOf: number | undefined
  totalSupply: number | undefined
  paused: boolean | undefined
  regularPrice: CurrencyAmount<Token> | undefined
  wlPrice: CurrencyAmount<Token> | undefined
  maxTotalSupply: number | undefined
}

// gets the staking info from the network for the active chain id
export function useNftInfo(): ERC721Info {
  const { chainId, account } = useWeb3React()

  const contractAddress = useMemo(
    () => (chainId ? NFT_CONTRACT_INFO[chainId].collection.address : undefined),
    [chainId]
  )
  const native = chainId ? nativeOnChain(chainId) : undefined

  const accountArg = useMemo(() => [account ?? undefined], [account])
  const erc721Contract = useERC721Contract(contractAddress)
  const balanceOf = useSingleCallResult(erc721Contract, 'balanceOf', accountArg)
  const totalSupply = useSingleCallResult(erc721Contract, 'totalSupply')
  const paused = useSingleCallResult(erc721Contract, 'paused')
  const regularPrice = useSingleCallResult(erc721Contract, 'regularMintPrice')
  const wlPrice = useSingleCallResult(erc721Contract, 'whitelistMintPrice')
  const maxTotalSupply = useSingleCallResult(erc721Contract, 'maxSupply')

  return useMemo(() => {
    const memo: ERC721Info = {
      contractAddress,
      balanceOf: undefined,
      totalSupply: undefined,
      paused: undefined,
      regularPrice: undefined,
      wlPrice: undefined,
      maxTotalSupply: undefined,
    }

    if (!chainId) return memo
    else {
      if (
        !balanceOf?.loading &&
        !totalSupply?.loading &&
        !paused?.loading &&
        !regularPrice?.loading &&
        !wlPrice?.loading &&
        !maxTotalSupply?.loading
      ) {
        if (
          balanceOf?.error ||
          totalSupply?.error ||
          paused?.error ||
          regularPrice?.error ||
          wlPrice?.error ||
          maxTotalSupply?.error
        ) {
          console.error('Failed to ERC721 info')
          return memo
        }
        memo.balanceOf = parseInt(balanceOf?.result?.[0].toString())
        memo.totalSupply = parseInt(totalSupply?.result?.[0].toString())
        memo.paused = paused?.result?.[0]
        memo.regularPrice = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(regularPrice?.result?.[0] ?? 0))
          : undefined
        memo.wlPrice = native
          ? CurrencyAmount.fromRawAmount(native.wrapped, JSBI.BigInt(wlPrice?.result?.[0] ?? 0))
          : undefined
        memo.maxTotalSupply = parseInt(maxTotalSupply?.result?.[0].toString())

        return memo
      }
      return memo
    }
  }, [
    contractAddress,
    chainId,
    balanceOf?.loading,
    balanceOf?.error,
    balanceOf?.result,
    totalSupply?.loading,
    totalSupply?.error,
    totalSupply?.result,
    paused?.loading,
    paused?.error,
    paused?.result,
    regularPrice?.loading,
    regularPrice?.error,
    regularPrice?.result,
    wlPrice?.loading,
    wlPrice?.error,
    wlPrice?.result,
    maxTotalSupply?.loading,
    maxTotalSupply?.error,
    maxTotalSupply?.result,
    native,
  ])
}
