import { TransactionResponse } from '@ethersproject/providers'
import { useWeb3React } from '@web3-react/core'
import { useMutantContract } from 'hooks/useContract'
import React, { useState } from 'react'
import { MutantInfo } from 'state/mutant/mutant'
import styled from 'styled-components/macro'

import { CloseIcon, ThemedText } from '../../theme'
import { ButtonError } from '../Button'
import { AutoColumn } from '../Column'
import Modal from '../Modal'
import { LoadingView, SubmittedView } from '../ModalViews'
import { RowBetween } from '../Row'

const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
`

interface ClaimModalProps {
  isOpen: boolean
  onDismiss: () => void
  mutantInfo: MutantInfo
}

export default function ClaimModal({ isOpen, onDismiss, mutantInfo }: ClaimModalProps) {
  const { account } = useWeb3React()

  // monitor call to help UI loading state
  const [hash, setHash] = useState<string | undefined>()
  const [attempting, setAttempting] = useState(false)

  function wrappedOnDismiss() {
    setHash(undefined)
    setAttempting(false)
    onDismiss()
  }

  const mutantContract = useMutantContract(mutantInfo.contractAddress)

  async function onClaimReward() {
    if (mutantContract && mutantInfo?.withdrawableDividend?.greaterThan('0')) {
      setAttempting(true)
      await mutantContract
        .withdrawDividend()
        .then((response: TransactionResponse) => {
          setHash(response.hash)
        })
        .catch((error: any) => {
          setAttempting(false)
          console.log(error)
        })
    }
  }

  let error: string | undefined
  if (!account) {
    error = 'Connect Wallet'
  }
  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOnDismiss} maxHeight={90}>
      {!attempting && !hash && (
        <ContentWrapper gap="lg">
          <RowBetween>
            <ThemedText.MediumHeader>Claim</ThemedText.MediumHeader>
            <CloseIcon onClick={wrappedOnDismiss} />
          </RowBetween>
          {mutantInfo?.withdrawableDividend && (
            <AutoColumn justify="center" gap="md">
              <ThemedText.BodyPrimary fontWeight={600} fontSize={36}>
                {mutantInfo?.withdrawableDividend?.toSignificant(6)}
              </ThemedText.BodyPrimary>
              <ThemedText.BodyPrimary>Unclaimed</ThemedText.BodyPrimary>
            </AutoColumn>
          )}
          <ThemedText.SubHeader style={{ textAlign: 'center' }}>
            When you claim without withdrawing, your liquidity remains in the pool.
          </ThemedText.SubHeader>
          <ButtonError disabled={!!error} error={!!error} onClick={onClaimReward}>
            {error ?? 'Claim'}
          </ButtonError>
        </ContentWrapper>
      )}
      {attempting && !hash && (
        <LoadingView onDismiss={wrappedOnDismiss}>
          <AutoColumn gap="12px" justify="center">
            <ThemedText.BodyPrimary fontSize={20}>
              Claiming {mutantInfo?.withdrawableDividend?.toSignificant(6)}
            </ThemedText.BodyPrimary>
          </AutoColumn>
        </LoadingView>
      )}
      {hash && (
        <SubmittedView onDismiss={wrappedOnDismiss} hash={hash}>
          <AutoColumn gap="12px" justify="center">
            <ThemedText.LargeHeader>Transaction Submitted</ThemedText.LargeHeader>
            <ThemedText.BodyPrimary fontSize={20}>Claimed !</ThemedText.BodyPrimary>
          </AutoColumn>
        </SubmittedView>
      )}
    </Modal>
  )
}
