import { Trans } from '@lingui/macro'
import { Trace } from '@uniswap/analytics'
import { InterfacePageName } from '@uniswap/analytics-events'
import { useWeb3React } from '@web3-react/core'
import { AutoColumn } from 'components/Column'
import DividendCard from 'components/Mutant/DividendCard'
import MintCard from 'components/Mutant/MintCard'
import MutatorCard from 'components/Mutant/MutatorCard'
import { RowBetween } from 'components/Row'
import { SwitchLocaleLink } from 'components/SwitchLocaleLink'
import { isSupportedChain } from 'constants/chains'
import { WrongNetworkCard } from 'pages/Pool'
import styled from 'styled-components/macro'
import { ThemedText } from 'theme'

const PageWrapper = styled(AutoColumn)`
  padding: 68px 8px 0px;
  max-width: 870px;
  width: 100%;

  ${({ theme }) => theme.deprecated_mediaWidth.deprecated_upToMedium`
    max-width: 800px;
  `};

  ${({ theme }) => theme.deprecated_mediaWidth.deprecated_upToSmall`
    max-width: 500px;
  `};

  @media only screen and (max-width: ${({ theme }) => `${theme.breakpoint.md}px`}) {
    padding-top: 48px;
  }

  @media only screen and (max-width: ${({ theme }) => `${theme.breakpoint.sm}px`}) {
    padding-top: 20px;
  }
`
const TitleRow = styled(RowBetween)`
  color: ${({ theme }) => theme.textSecondary};
  ${({ theme }) => theme.deprecated_mediaWidth.deprecated_upToSmall`
    flex-wrap: wrap;
    gap: 12px;
    width: 100%;
  `};
`

export default function Mutant() {
  const { chainId } = useWeb3React()

  if (!isSupportedChain(chainId)) {
    return <WrongNetworkCard />
  }

  return (
    <Trace page={InterfacePageName.POOL_PAGE} shouldLogImpression>
      <PageWrapper>
        <AutoColumn gap="lg" justify="center">
          <AutoColumn gap="lg" style={{ width: '100%' }}>
            <TitleRow padding="0">
              <ThemedText.LargeHeader>
                <Trans>Mint NFT</Trans>
              </ThemedText.LargeHeader>
            </TitleRow>

            <MintCard></MintCard>

            <TitleRow padding="0">
              <ThemedText.LargeHeader>
                <Trans>Mutator</Trans>
              </ThemedText.LargeHeader>
            </TitleRow>

            <MutatorCard></MutatorCard>

            <TitleRow padding="0">
              <ThemedText.LargeHeader>
                <Trans>Dividend</Trans>
              </ThemedText.LargeHeader>
            </TitleRow>

            <DividendCard></DividendCard>
          </AutoColumn>
        </AutoColumn>
      </PageWrapper>
      <SwitchLocaleLink />
    </Trace>
  )
}
